.product-slider {
  position: relative;
  z-index: 1;


  &__nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
  }

  &__pagination {
    display: flex;
    padding: 0 15px;

    .swiper-pagination-bullet {
      background-color: #808080;
      margin: 0 3px;

      &-active {
        background-color: #808080;
      }
    }
  }

  &__prev,
  &__next {
    display: flex;
    cursor: pointer;

    &:focus {
      outline: 0;
    }

    &.swiper-button-disabled {
      opacity: 0.3;
    }
  }

  @media (max-width: 767px) {
    &__nav {
      margin-top: 30px;
    }
  }
}
