.pagination {
  margin-bottom: 0;

  .page-link {
    padding: 0 10px;
    background: 0 !important;
    border: 0 !important;
    color: #999;
    font-size: 14px;
    text-transform: uppercase;

    &:hover {
      color: #000;
    }
  }

  .page-item.active .page-link {
    color: #000;
  }
}
