.home-banner {
  display: block;
  width: 100%;
}

.product-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;

  @media (max-width: 991px) {
    grid-gap: 16px;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr 1fr;
  }
}

.filter-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 20px;
  background-color: $light;
  margin-bottom: 30px;
}

.filter {
  display: flex;

  &__item {
    position: relative;
    overflow: hidden;
    margin-bottom: 0;
    cursor: pointer;
    color: #999;

    input {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      opacity: 0;
    }

    input:checked+span {
      color: #000;
    }

    &:not(:last-child) {
      margin-right: 36px;
    }
  }

  @media (max-width: 767px) {
    display: block;
    flex: 0 0 100%;
    padding-bottom: 6px;
    white-space: nowrap;
    overflow: auto;

    &__item {
      display: inline-block;

      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }
}

.product-info {
  margin-bottom: 80px;

  &__title {
    font-size: 16px;
  }

  &__info {
    width: 100%;
    margin-bottom: 40px;

    td {
      vertical-align: middle;
      padding: 4px 0;

      &:first-child {
        min-width: 110px;
        padding-right: 20px;
        white-space: nowrap;
      }
    }
  }

  &__break {
    height: 45px;
  }

  .custom-select {
    border-radius: 0;

    &:focus {
      box-shadow: unset;
    }
  }

  @media (max-width: 991px) {
    margin-bottom: 60px;
  }

  @media (max-width: 767px) {
    margin-bottom: 40px;

    &__info {
      margin-bottom: 20px;

      td {
        &:first-child {
          min-width: unset;
        }
      }
    }

    &__break {
      height: 20px;
    }
  }
}

.product-desc {
  margin-bottom: 70px;

  &__title {
    display: flex;
    box-shadow: inset 0 -1px 0 $border-color;
    font-size: 12px;
    margin-bottom: 30px;

    span {
      display: inline-block;
      width: 300px;
      max-width: 100%;
      text-align: center;
      border: 1px solid $border-color;
      border-bottom: 0;
      background-color: $white;
      padding: 12px;
    }
  }

  &__content {
    font-size: 12px;

    @media (max-width: 991px) {
      margin-bottom: 60px;
    }

    @media (max-width: 767px) {
      margin-bottom: 40px;
    }
  }
}

.product-related {
  margin-bottom: 70px;

  &__title {
    font-size: 16px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 35px;
  }

  @media (max-width: 991px) {
    margin-bottom: 60px;
  }

  @media (max-width: 767px) {
    margin-bottom: 40px;

    &__title {
      margin-bottom: 24px;
    }
  }
}

.button {
  display: inline-block;
  font-size: 14px;
  line-height: 43px;
  padding: 0 40px;
  border: 1px solid $border-color;
  color: $gray-800;
  text-transform: uppercase;

  &:hover {
    text-decoration: none;
    color: #000;
    border-color: #000;
  }

  &--block {
    display: block;
    text-align: center;
  }

  &--dark {
    @include btn('shine');
    display: block;
    background-color: #000;
    border-color: #000;
    color: $white;

    &:hover {
      color: $white;
    }
  }
}
