.product {
  position: relative;
  z-index: 1;

  &__wrapper {
    overflow: hidden;
    position: relative;
  }

  &__frame {
    @include frame('rect', 125%);
  }

  &__btns {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%) translateY(50%);
    z-index: 20;
    background-color: rgba(#fff, 0.7);
    padding: 12px 0;
    transition: 0.4s;
    opacity: 0;
  }

  &__wrapper:hover &__btns {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }

  &__btn {
    display: inline-flex;
    width: 16px;
    height: 16px;
    justify-content: center;
    align-items: center;
    opacity: 0.5;
    transition: 0.3s;

    &:hover {
      opacity: 1;
    }

    &:not(:last-child) {
      margin-right: 10px;
    }

    img {
      height: 16px;
      object-fit: contain;
      object-position: 50% 50%;
      position: relative;
      top: -1px;
    }

    &:nth-child(2) img {
      top: 1px;
    }
  }

  &__title {
    font-size: 12px;
    margin: 16px 0 4px;
    text-align: center;

    a:hover {
      color: #000;
    }
  }

  &__price {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 12px;
  }

  &__saleoff {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    color: $white;
    background-color: #000;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
  }
}

.news {
  &__frame {
    @include frame('rect', 100%);
    @include frame('zoom');
  }

  &__title {
    font-size: 14px;
    margin: 16px 0 10px;

    a:hover {
      color: #000;
    }
  }

  &__time {
    font-size: 12px;
    color: #999;
    margin-bottom: 10px;
  }

  &__desc {
    @include max-line(4);
    font-size: 12px;
  }
}
