.footer {
  border-top: 1px solid $border-color;
  font-size: 12px;
  color: #888;

  a {
    color: #888;
  }

  a:hover {
    color: #000;
  }

  &__nav {
    border-bottom: 1px solid $border-color;
  }

  &__nav-inner {
    display: flex;
    flex-wrap: wrap;
  }

  &__nav-item {
    display: inline-block;
    font-size: 12px;
    text-transform: uppercase;
    color: #888;
    line-height: 52px;

    &:hover {
      color: #000;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 50px;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 620px;
    grid-gap: 50px;
    padding: 10px 0 30px;
    margin: 0 auto;
  }

  @media (max-width: 575px) {
    &__nav-inner {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }

    &__nav-item {
      text-align: center;
      line-height: 40px;

      &:not(:last-child) {
        margin-right: 0;
      }
    }

    &__grid {
      grid-template-columns: 1fr;
      grid-gap: 30px;
      padding: 30px 0;
    }
  }
}
