.section {
  margin-bottom: 70px;

  &__title {
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 50px;

    span {
      display: inline-block;
      min-width: 250px;
      border-top: 1px solid $border-color;
      border-bottom: 1px solid $border-color;
      padding: 15px 0;
    }
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 18px;
      margin-bottom: 30px;

      span {
        padding: 12px 0;
      }
    }
  }
}

.section-2 {
  margin-bottom: 40px;

  &__title {
    font-size: 20px;
    text-transform: uppercase;
    margin-bottom: 10px;
  }
}

.section-3 {
  border: 1px solid $border-color;
  border-radius: 4px;

  &__title {
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    background-color: $light;
    padding: 8px 20px;
    border-bottom: 1px solid $border-color;
  }

  &__body {
    padding: 20px;
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 16px;
  }
}
