.logo {
  display: inline-flex;
  margin: 10px 0;
  font-size: 32px;
  text-transform: uppercase;
  color: #000;
  font-weight: 500;

  &:hover {
    color: #000;
    text-decoration: none;
  }
}

.header {
  position: relative;
  z-index: 100;

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__elements {
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__btn {
    display: inline-flex;
    height: 35px;
    justify-content: center;
    align-items: center;
    position: relative;
    color: $gray-800;
    font-size: 16px;
    text-transform: uppercase;

    img {
      height: 20px;
    }

    .fa {
      font-size: 20px;
    }

    &+& {
      margin-left: 16px;
    }

    &:hover {
      color: #000;
      text-decoration: none;
    }

    &--hotline {
      display: none;
    }
  }

  @media (min-width: 768px) {
    &__btn {
      font-size: 12px;

      &--hotline {
        display: inline-flex;
      }
    }
  }

  @media (min-width: 1200px) {
    height: 160px;

    &__wrapper {
      width: 100%;
      position: fixed;
      top: 0;
      left: 0;
    }

    &__inner {
      height: 160px;
      align-items: center;
    }

    &__logo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &__nav {
      width: 160px;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.navigation {
  &__inner {
    position: relative;
  }
}

.search {
  padding: 10px 20px 20px;
  border-bottom: 1px solid $border-color;
  margin-bottom: 20px;

  .form-control {
    border-radius: 0;
    border: 0;
    border-bottom: 1px solid #000;
  }

  .input-group-text {
    background-color: $white;
    border: 0;

    img {
      height: 28px;
    }
  }

  @media (min-width: 1200px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 0;
    padding: 0;
    margin: 0;
    height: 160px;

    .form-control {
      padding: 0;
    }

    .input-group-text {
      padding: 0 10px;

      img {
        height: 16px;
      }
    }
  }
}
