.post {
  border: 1px solid $border-color;
  padding: 20px;
  margin-bottom: 60px;

  &-title {
    font-size: 14px;
    color: #000;
    text-transform: uppercase;
    margin-bottom: 16px;
  }

  &-time {
    font-size: 12px;
    color: #999;
    margin-bottom: 16px;
  }

  &-sapo {
    font-size: 12px;
    color: #000;
    margin-bottom: 16px;
  }

  &-content {
    font-size: 12px;

    p:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  @media (max-width: 767px) {
    margin-bottom: 40px;
  }
}
