.cart {
  &__frame {
    width: 70px;
    margin-right: 20px;
    @include frame('rect', 125%);
    @include frame('zoom');
  }

  &__title {
    a {
      color: #000;

      &:hover {
        color: #000;
        text-decoration: none;
      }
    }
  }

  &__remove {
    color: #000;

    &:hover {
      color: $danger;
      text-decoration: none;
    }
  }

  &__subtotal {
    font-size: 20px;
    font-weight: 700;
    color: $danger;
  }

  td {
    vertical-align: middle;
  }

  @media (max-width: 991px) {
    thead {
      display: none;
    }

    table,
    tbody,
    tfoot,
    tr,
    td {
      display: block;
    }

    tr {
      &:hover {
        background: 0 !important;
      }

      &:nth-child(even) {
        background-color: $light;

        &:hover {
          background: $light !important;
        }
      }
    }

    td {

      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5),
      &:nth-child(6) {
        position: relative;
        padding-left: 120px;

        &::before {
          content: attr(data-label);
          display: flex;
          height: 100%;
          width: 120px;
          padding: 8px;
          align-items: center;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }
}
